@mixin close-button($margin-right: 0, $color: var(--#{$cdk}primary-500)) {
  .close {
    margin-right: $margin-right;
    color: $color;
    cursor: pointer;
    opacity: 1;

    .material-icons {
      font-size: var(--#{$cdk}size-18);
      vertical-align: middle;
    }
  }
}

;@import "sass-embedded-legacy-load-done:95";