// Breadcrumb
.breadcrumb {
  margin: 0;

  li {
    + li {
      padding-right: 0;
      padding-left: var(--#{$cdk}size-8);

      &::before {
        float: left;
        padding-right: var(--#{$cdk}size-8);
        content: ">";
      }
    }
  }
}

